import { LoaderFunction, useLoaderData } from "remix";
import type { Post } from "~/post";

export const loader: LoaderFunction = ({ params }) => {
  return {
    slug: params.slug,
    title: "Worked!",
  };
};

export default function Post() {
  const post: Post = useLoaderData();

  return (
    <div>
      <div>{post.slug}</div>
      <div>{post.title}</div>
    </div>
  );
}
